@charset "UTF-8";
/*サイト個別設定*/
@keyframes fadeIn {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@keyframes fadeOut {
  from {
    opacity: 1; }
  to {
    opacity: 0; } }

@keyframes spin {
  0% {
    transform: rotate(0deg); }
  100% {
    transform: rotate(360deg); } }

a {
  color: #53a7db; }

.globalHeader__logo {
  width: 75px; }
  @media all and (min-width: 781px) {
    .globalHeader__logo {
      width: 212px; } }

.globalNavi {
  background-color: #53a7db;
  color: #fff; }
  .globalNavi__childItem a span {
    color: #53a7db; }
  .globalNavi__item--hasChild.-active > span {
    color: #53a7db; }
    .globalNavi__item--hasChild.-active > span::after {
      border-top-color: #53a7db; }
